@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100..900&family=Oswald:wght@200..700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
* {
  box-sizing: border-box;
}

body {
  font-family: "Oswald", sans-serif;
  margin: 0;
  background-image: url('./it.bg2.jpg');
  letter-spacing: .5px;
  overflow-y: hidden;
}

.container {
  width: 100%;
  height: 100vh;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  backdrop-filter: blur(10px);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
}

.site-title {
  font-size: 2rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  margin: 10px;

}

.nav a {
  color: inherit;
  text-decoration: none;
  display: flex;
  align-items: center;
}

.nav a:hover {
  color: black;
}
.nav .site-title img {
  height: 60px;
}

.nav ul {
  font-size: 26px;
}

.icon {
  font-size: 200px;
  padding-bottom: 22px;
  margin-right: 20px;
}

.pages span {
  letter-spacing: 15px;
  font-size: 250px;
  font-weight: 500;
  white-space:inherit;
  display: inline-block;
  transform: translateY(-100vh);
  animation: fall 1.5s forwards;
}

@keyframes fall {
  100%{
    transform: translateY(0);
  }
}

.pages span:nth-child(2) {
  animation-delay: 0.25s;
}

.pages span:nth-child(3) {
  animation-delay: 0.35s;
}

.pages span:nth-child(4) {
  animation-delay: 0.45s;
}

.pages span:nth-child(5) {
  animation-delay: 0.55s;
}

.pages span:nth-child(6) {
  animation-delay: 0.65s;
}

.pages span:nth-child(7) {
  animation-delay: 0.75s;
}

.pages span:nth-child(8) {
  animation-delay: 0.85s;
}

.pages span:nth-child(9) {
  animation-delay: 0.95s;
}

.pages span:nth-child(10) {
  animation-delay: 1.05s;
}

